






































/* eslint-disable */
import Vue, { PropType } from 'vue';
import { mapActions } from 'vuex';
import { validateEmail } from '@/utils/validate-email';

export default Vue.extend({
  name: 'es-forgot-password-modal',

  model: {
    prop: 'is-open',
    event: 'is-open',
  }, // model

  props: {
    isOpen: {
      required: true,
      type: Boolean as PropType<boolean>,
    },
  },

  data: () => ({
    email: null,
  }),

  methods: {
    ...mapActions({
      forgotPassword: 'session/forgotPassword',
    }),
    async onSubmit() {
      if (validateEmail(this.email)) {
        await this.forgotPassword(this.email);
        (this as any).$toasts.toast({
          id: 'forgot-password',
          title: this.$t('toast.warning_title'),
          message: this.$t('toast.password_reset'),
          intent: 'warning',
        });
        this.$emit('is-open', false);
      } else {
        (this as any).$toasts.toast({
          id: 'forgot-password',
          title: this.$t('toast.warning_title'),
          message: this.$t('toast.invalid_email'),
          intent: 'warning',
        });
      }
    },
  },
});
