














import Vue from 'vue';

export default Vue.extend({
  name: 'es-skeleton',

  props: {
    border: {
      default: false,
      type: Boolean,
    },

    padding: {
      default: null,
      type: String,
      validator: value => [
        'xs',
        'sm',
        'md',
      ].includes(value),
    },
  }, // props
});
